import React, { useEffect, useState } from "react";
import Table from "../../components/table";
import useUser from "../../store/users";
import Button from "../../components/button";
import Input from "../../components/input";
import { toast } from "react-toastify";
import { IconButton, Stack, Tab, Tabs, Typography } from "@mui/material";
import { Delete } from "@mui/icons-material";
import EditIcon from '@mui/icons-material/Edit';
import Category from "../../components/UI/Category";
import UploadCsv from "../store/UploadCsv";
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import Tooltip from '@mui/material/Tooltip';
import moment from "moment/moment";
import PostAddIcon from '@mui/icons-material/PostAdd';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import useSkill from "../../store/skill";
import { COLORS } from "../../helpers/constants";

const TestUserPage = () => {
  const [tab, setTab] = useState("Users")
  const [users, setUsers] = useState([]);
  const [usersF, setUsersF] = useState([]);
  const [operators, setOperators] = useState([]);
  const [operatorsF, setOperatorsF] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [modalOpen, setModalOpen] = useState(false);
  const [csvData, setCsvData] = useState([])
  const [page, setPage] = useState(0);
  const [file, setFile] = useState(null);
  const [data, setData] = useState({
  });
  const [skills, setSkills] = useState([])
  const [userId, setUserId] = useState('')
  const [superVisor, setSuperVisor] = useState([])
  const [searchInput, setSearchInput] = useState('')
  const [op_id, setOp_id] = useState('')
  const [edit, setEdit] = useState(false)
  const regex = /^[A-Za-z\s]*$/;

  const { list, create, updateProfile, update, userProfile, remove, listByUserName, listByAadhar, updateOpSkill, createByCsv, updateProfileCSV } = useUser();
  const { list: skill_list } = useSkill()
  useEffect(() => {
    setUsers([])
    fetchUsers();
  }, [page, tab]);

  useEffect(() => {
    fetchSkill();
  }, []);

  const fetchSkill = async () => {
    const [list] = await skill_list(page + 1);
    const newUsers = list.results.map(item => ({
      value: item.skill_name,
      key: item.id
    }));
    setSkills(prevUsers => [...prevUsers, ...newUsers]);
  };

  const fetchUsers = async () => {
    setLoading("fetch");
    try {
      const [_list, _count] = await list(page + 1);
      const newOptr = _list.filter(item => item.is_operator);
      const newUsr = _list.filter(item => !item.is_operator);
      const activeUsr = _list.filter(item => !item.is_operator && item.status === true);
      const superList = activeUsr.map(item => ({
        value: item.username,
        key: item.id
      }));
      setSuperVisor(superList)
      setUsers(newUsr);
      setUsersF(newUsr);
      setTotal(_count);
      const operatorProfiles = await Promise.all(
        newOptr.map(item => userProfile(item.id))
      );
      setOperators(operatorProfiles);
      setOperatorsF(operatorProfiles);
    } catch (error) {
      console.error("Error fetching users:", error);
    } finally {
      setLoading(false);
    }
  };

  const fetchByName = async (name) => {
    let tempUsers = usersF
    setLoading("fetch")
    if (name.length !== 0) {
      const [_list] = await listByUserName(name);
      if (_list !== undefined) {
        setUsers([_list]);
      }else{
        setUsers([])
      }
    }
    else {
      setUsers(tempUsers)
    }
    setLoading(false);
  }

  const fetchByAadhar = async (aadhar) => {
    let tempOperator = operatorsF
    setLoading("fetch")
    if (aadhar !== '') {
      const [_list, _count] = await listByAadhar(aadhar);
      if (_list !== undefined) {
        setOperators([_list])
      }else{
        setOperators([])
      }
    } else {
      setOperators(tempOperator)
    }
    setLoading(false)
  }


  const handleDelete = async (id) => {
    setLoading("fetch");
    // tab === "Users" ? await remove(id): await removeOperator(id);
    tab === "Users" ? await remove(id) : await remove(id);
    await fetchUsers();
    setLoading(false);
  };

  const handleUpdate = async (id, is_admin, type) => {
    setLoading("fetch");
    const payload = {
      "is_admin": is_admin === true ? false : true
    }
    const payload2 = {
      "status": is_admin === true ? false : true
    }
    // tab === "Users" ? await update(id,type==="status"?payload2:payload):updateOperator(id,type==="status"?payload2:payload)
    tab === "Users" ? await update(id, type === "status" ? payload2 : payload) : update(id, type === "status" ? payload2 : payload)
    await fetchUsers();
    setLoading(false);
  };

  const COLUMNS = [
    {
      id: 1,
      title: "Email",
      render: (rowData) => rowData.email,
    },
    {
      id: 2,
      title: "User Name",
      render: (rowData) => rowData.username,
    },
    {
      id: 3,
      title: "Full Name",
      render: (rowData) => rowData.fullname,
    },
    // {
    //   id: 4,
    //   title: "Employee Id",
    //   render: (rowData) => rowData.employee_id,
    // },
    {
      id: 5,
      title: "Role",
      render: (rowData) => <Category title={rowData.is_admin ? "admin" : "Na"} />,
    },
  ];



 const handleChange=(e)=> {
    const value = e.target.value;
    console.log("e.target.value",e.target.value.length)
    if (tab === "Users") {
      fetchByName(value)
    } else if(tab === "Operators") {
      fetchByAadhar(value)
    }
    setSearchInput(value);
  }

  return (
    <div className="mx-3 my-2 fw-500">
        <Typography className="fw-20-500-36">Test Result</Typography>
      <div className="flex items-center justify-between">
        {/* <Stack mb={1}>
          <Input
            height="37px"
            value={searchInput}
            placeholder={tab === "Users" ? "Search By username" : 'Search by Aadhar Number'}
            onChange={(e) => handleChange(e)}
          />
        </Stack> */}
       
      </div>
      <Table
        columns={COLUMNS}
        rows={tab === "Users" ? users : operators}
        rowsPerPage={10}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        page={page}
        setPage={setPage}
        paginator
        serialize
        total={total}
        tableBodyColor={COLORS.WHITE}
      />
    </div>
  );
};

export default TestUserPage;
